<template>
  <div>
    <div>
      <h2>前言</h2>
      <p>对于一个多人团队来说，制定一个统一的规范是必要的，因为个性化的东西无法产生良好的聚合效果，规范化可以提高编码工作效率，使代码保持统一的风格，以便于代码整合和后期维护。请参与开发相关人员遵循，同时也请各位日后进行补充完善。</p>
      <h2>HTML规范</h2>
      <ul>
        <li>嵌套的节点应该缩进；</li>
        <li>在属性上，使用双引号，不要使用单引号；</li>
        <li>属性名全小写，用中划线做分隔符；</li>
        <li>用
          <code>&lt;meta></code>标签可以指定页面应该用什么版本的IE来渲染
          <code>
            &lt;meta
            http-equiv="X-UA-Compatible"
            content="IE=Edge"
            >
          </code>
        </li>
        <li>单页面应用如非必要，禁止使用id</li>

      </ul>
      <h2>CSS规范</h2>
      <ul>
        <li>禁止使用层级过深的选择器，最多4级。</li>
        <p>错误示例</p>
        <PrismEditor
          class="csstext"
          :code="classcode"
        ></PrismEditor>
        <li> 除非有特定的功能、组件要求等，禁止随意使用id来定义元素样式；</li>
        <li> class类名的英文单词用-连接, react项目由于class名需要被用作js变量故改 - 为 _ ;</li>
        <li>
          <p>属性顺序尽量按照；<br>
            1 位置属性(position、top、right、z-index、display、float等)；<br>
            2 大小(width, height, padding, margin等)；<br>
            3 文字系列(font、line-height、letter-spacing、color、text-align等)；<br>
            4 背景(background、border等)；<br>
            5 其他(animation、transition等)。</p>
        </li>
        <li> 取值0和1之间的数值时，省略0不写</li>
        <PrismEditor
          class="csstext"
          :code="sizecode"
        ></PrismEditor>
        <li>缩写色值 (某些可以缩写的色值(aabbcc型或者aaaaaa型)使用缩写)</li>
        <PrismEditor
          class="csstext"
          :code="colorcode"
        ></PrismEditor>
        <li>值为0时，不要带单位</li>
        <PrismEditor
          class="csstext"
          :code="zerocode"
        ></PrismEditor>
        <li>尽量少用 * 选择器;</li>
        <li>css预编译器方面，由于公司大部分项目选用了less，所以如非必要首选less;</li>
        <li>css类名命名如果词穷可以参考：</li>
      </ul>
      <h2>javascript规范</h2>
      <ul>
        <li>代码注意缩进，保持代码美观，删除多余代码；</li>
        <li>变量命名遵循驼峰规则；</li>
        <li>调试完成后删除 console、debugger等调试代码；</li>
        <li>运算符前后加上空格。 例如: + - > &lt; = == ? : || &&</li>
        <li>字符串引号 首选 ' ' ，复杂的字符串拼接则首选 ` `</li>
        <li>如非必要始终使用 === 替代 ==; </li>
        <PrismEditor
          class="jstext"
          :code="signcode"
        ></PrismEditor>
        <li>始终将逗号置于行末 eslint: comma-style</li>
        <PrismEditor
          class="jstext"
          :code="commacode"
        ></PrismEditor>
        <li>尽量使用ES6语法，减少ES5语法；（特殊情况除外）</li>
        <ul style="list-style: revert;">
          <li>使用let、const代替var定义变量，变量用let,常量用const,全局常量定义时使用大写；</li>
          <li>尽量使用箭头函数 => 代替 function 定义函数，方便处理this；</li>
          <PrismEditor
            class="jstext"
            :code="funcode"
          ></PrismEditor>
          <li>如非必要尽量使用 forEach,map,filter,some,every,reduce 等声明式的数组操作方法替代 for 循环操作；（for循环容易增加代码复杂性减少可读性）</li>
          <PrismEditor
            class="jstext"
            :code="arrcode"
          ></PrismEditor>
          <li>for...in遍历对象时 使用hasOwnProperty保证代码逻辑严谨正确。</li>
          <PrismEditor
            class="jstext"
            :code="forcode"
          ></PrismEditor>
        </ul>
        <li>if else 不要嵌套太多层。 </li>
        <li>统一处理ajax；</li>
        <li>与业务联系不大，纯功能性的复杂逻辑，请封装好了放在其他文件再引入组件内使用，不要糅合在业务逻辑里，增加维护难度；</li>
        <li>复杂逻辑写好逻辑实现的注释</li>
      </ul>

    </div>
  </div>
</template>

<script>
import PrismEditor from "./codeBox.vue";
export default {
  name: "",
  components: { PrismEditor },
  props: {},
  data() {
    return {
      classcode: `.without-animation .book-body .body-inner .book-header .dropdown .dropdown-menu .buttons{}
.without-animation .book-body .body-inner .book-header .dropdown .dropdown-right .buttons{}
.without-animation .book-body .body-inner .book-header .pull-left .dropdown-menu .buttons{}`,
      sizecode: `.btn-cancel {
   font-size:0.875rem;
}✗
.btn-cancel {
   font-size:.875rem;
}✓`,
      colorcode: `.btn-cancel {
   background-color:#eebbcc;
} ✗
.btn-cancel {
   background-color:#ebc;
} ✓`,
      zerocode: `.btn-cancel {
   padding: 0px;
} ✗
.btn-cancel {
   padding: 0;
} ✓`,
      signcode: `  if (name === 'John')   // ✓
  if (name == 'John')    // ✗ 
  
  if (name !== 'John')   // ✓
  if (name != 'John')    // ✗ `,
      commacode: ` let obj = {
    foo: 'foo'
    ,bar: 'bar'   // ✗ 
  }

 let obj = {
    foo: 'foo',
    bar: 'bar'   // ✓
 }`,
      funcode: `// ✗ 
function Person() {
  this.age = 0;
  let _this = this;
  setInterval(function growUp() {
    _this.age++;
    console.log(_this.age); 
  }, 1000);   
}
let p = new Person();

// ✓
function Person() {
  this.age = 0;
  setInterval(()=> {
    this.age++;
    console.log(this.age); // 1 2 3...
  }, 1000);
}
var p = new Person();`,
      arrcode: `let a = [1,2,3,4,5];
// ✗
for(let i = 0;i<a.length;i++){
    console.log(a[i]);
}
// ✓
a.forEach(e=>{
    console.log(e);
})
// ✓
a.map(e=>{
    console.log(e);
}); `,
      forcode: `let a = {name:'Lucy',age:32,sex:'女'};
// ✗
for(o in a){
    console.log(a[o]);
}
// ✓
for(o in a){
    if(a.hasOwnProperty(o)){
        console.log(a[o]);
    }
}`,

      lineNumbers: true, // true为编辑模式， false只展示不可编辑
    };
  },
  computed: {},
  filter: {},
  activated: {},
  mounted() {},
  methods: {},
};
</script>

<style lang='less' scoped>
.csstext::before {
  content: "css代码";
}
.jstext::before {
  content: "js代码";
}
</style>
